<template>
  <div class="FirstEquipList mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input v-model="term.name" placeholder="" @keyup.enter.native="getequiplist(1)" clearable></el-input>
        <el-input v-model="term.serialNo" placeholder="" @keyup.enter.native="getequiplist(1)" clearable></el-input>
        <el-select v-model="term.status" clearable filterable placeholder="">
          <el-option
            v-for="item in statuslist"
            :key="item.value"
            :label="item.label"
            :value="item.id"
          ></el-option>
        </el-select>
        <el-button type="primary" @click="getequiplist(1)"></el-button>
      </div>
    </div>
    <el-table
      :data="equiplist"
      :height="this.$store.state.tableHeight"
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text=""
    >
      <el-table-column prop="serialNo" align="center" label=""></el-table-column>
      <el-table-column prop="name" align="center" label=""></el-table-column>
      <el-table-column prop="hotelName" align="center" label=""></el-table-column>
      <el-table-column prop="roomNo" align="center" label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          {{scope.row.type=='1'?'6':scope.row.type=='2'?'8':'10'}}
        </template>
      </el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-tag :type="scope.row.status==1?'':'info'">{{scope.row.status | fiterstatus}}</el-tag>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[15, 30, 50, 100]"
        :page-size="15"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>
  </div>
</template>

<script>
export default {
  name: "FirstEquipList",
  data() {
    return {
      total: 0,
      pageNum: 1,
      pageSize:15,
      loading: false,
      term:{
        name:"",
        serialNo:"",
        status:""
      },
      equiplist: [],
      statuslist: [
        {
          id: 1,
          label: ""
        },
        {
          id: 2,
          label: ""
        },
        {
          id: 3,
          label: ""
        }
      ]
    };
  },
  filters: {
    fiterstatus: function(val) {
      if (val == 1) {
        return "";
      } else if (val == 2) {
        return "";
      } else if (val == 3) {
        return "";
      }
    }
  },
  methods: {
    //
    getequiplist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url =
        "equipInitial?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      for (let key in this.term) {
        if (this.term[key]) {
          _Url += "&" + key + "=" + this.term[key];
        }
      }
      this.loading=true;
      this.$http.get(_Url).then(res => {
        this.loading=false;
        this.equiplist = res.data.data;
        this.total = res.data.total;
      });
    },
    //
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getequiplist();
    },
    //
    handleSizeChange(val) {
      this.pageSize = val;
      this.getequiplist();
    }
  },
  created() {
    console.log("fdasfds");
    this.getequiplist();
  }
};
</script>

<style lang="less">
.FirstEquipList {
  .topleft {
      .el-input {
        width: 25%;
      }
    }
}
</style>


