<template>
  <div class="supplier_list mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input v-model="supplierName" placeholder="" @keyup.enter.native="getsupplylist(1)" clearable></el-input>
        <el-button type="primary" @click="getsupplylist(1)"></el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)"></el-button>
      </div>
    </div>
    <el-table
      :data="supplylist"
      :height="this.$store.state.tableHeight"
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text=""
    >
      <el-table-column prop="name" align="center" label=""></el-table-column>
      <el-table-column prop="contactName" align="center" label=""></el-table-column>
      <el-table-column prop="contactTel" align="center" label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-tag :type="scope.row.status==1?'':'info'">{{scope.row.status | fiterstatus}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" align="center">
        <template slot-scope="scope">
          <el-button
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[15, 30, 50, 100]"
        :page-size="15"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      :title="disval==1?'':''"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name" placeholder="" maxlength="100" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="contactName">
          <el-input v-model="ruleForm.contactName" placeholder="" maxlength="10" clearable></el-input>
        </el-form-item>

        <el-form-item label="" prop="contactTel">
          <el-input v-model="ruleForm.contactTel" placeholder="" type="number" oninput="if(value.length>11)value=value.slice(0,11)" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="ruleForm.status" placeholder="">
            <el-option
              v-for="item in statuslist"
              :key="item.id"
              :label="item.label"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
var checkPhone = (rule, value, callback) => {
  const tel = /^0\d{2,3}-?\d{7,8}$/;
  const phone = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
  if (!value) {
    return callback(new Error(""));
  }
  setTimeout(() => {
    if (!Number.isInteger(+value)) {
      callback(new Error(""));
    } else {
      if (tel.test(value)) {
        callback();
      } else if (phone.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    }
  }, 100);
};
export default {
  name: "supplier_list",
  data() {
    return {
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 15,
      supplierName: "",
      centerDialogVisible: false,
      actuserobj:{},
      ruleForm: {
        contactName: "",
        contactTel: "",
        id: "",
        name: "",
        remark: "",
        status: ""
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        contactTel: [
          { required: true, validator: checkPhone, trigger: "blur" }
        ],
        contactName: [
          { required: true, message: "", trigger: "blur" }
        ],
        status: [{ required: true, message: "", trigger: "change" }]
      },
      supplylist: [],
      statuslist: [
        {
          id: 1,
          label: ""
        },
        {
          id: 2,
          label: ""
        }
      ]
    };
  },
  filters: {
    fiterstatus: function(val) {
      if (val == 1) {
        return "";
      } else {
        return "";
      }
    }
  },
  watch:{
    centerDialogVisible:function(){
      if(!this.centerDialogVisible){
        for (let key in this.ruleForm) {
          if (this.ruleForm[key]) {
            this.ruleForm[key] = "";
          }
        }
        this.actuserobj={};
      }
    }

  },
  methods: {
    //
    getsupplylist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url =
        "supply?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      if (this.supplierName) {
        _Url += "&name=" + this.supplierName;
      }
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        this.total = res.data.total;
        if (res.data.total < 1) {
         this.$message("");
          this.supplylist = [];
        } else {
          this.supplylist = res.data.data;
        }
      });
    },
    //
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getsupplylist();
    },
    //
    handleSizeChange(val) {
      this.pageSize = val;
      this.getsupplylist();
    },
    //
    openDialog(val) {
      this.disval = val;
      this.centerDialogVisible = true;
    },
    //
    handleedit(obj) {
      // this.ruleForm = obj;
      this.actuserobj=obj;
      this.getidinfo(obj.id);
      this.openDialog(2);
    },
    //ID
    getidinfo(id) {
      this.$http.get("supply/" + id).then(res => {
        let obj = res.data;
        obj.remark=obj.remark?obj.remark:"";
        for (let key in obj) {
          // if (obj[key]) {
            this.ruleForm[key] = obj[key];
          // }
        }
      });
    },
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savedata();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    resetForm(formName) {
       if(this.actuserobj && this.actuserobj.id){
         this.getidinfo(this.actuserobj.id);
       }else {
         this.$refs[formName].resetFields();
       }
    },
    //
    savedata() {
      let _Url = "supply",
        _parms = {};
      for (let key in this.ruleForm) {
        // if (this.ruleForm[key]) {
        _parms[key] = this.ruleForm[key];
        // }
      }
      if (this.disval == 1) {
        this.$http.post(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getsupplylist();
          }
        });
      } else if (this.disval == 2) {
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getsupplylist();
          }
        });
      }
    }
  },
  created() {
    this.getsupplylist();
  }
};
</script>

<style lang="less">
.supplier_list {
  .topleft {
      .el-input {
        width: 70%;
      }
    }
}
</style>




