<template>
  <div class="hotel_list mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="term.name"
          placeholder=""
          @keyup.enter.native="gethotellist(1)"
          clearable
        ></el-input>
        <el-select v-model="term.type" clearable filterable placeholder="">
          <el-option v-for="item in typelist" :key="item.id" :label="item.label" :value="item.id"></el-option>
        </el-select>
        <el-select v-model="term.status" clearable filterable placeholder="">
          <el-option
            v-for="item in statuslist"
            :key="item.name"
            :label="item.label"
            :value="item.id"
          ></el-option>
        </el-select>
        <el-button type="primary" @click="gethotellist(1)"></el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)"></el-button>
      </div>
    </div>
    <el-table
      :data="hotellist"
      :height="this.$store.state.tableHeight"
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text=""
    >
      <el-table-column prop="name" align="center" label=""></el-table-column>
      <el-table-column prop="address" align="center" label=""></el-table-column>
      <el-table-column prop="roomNo" align="center" label=""></el-table-column>
      <el-table-column prop="contactName" align="center" label=""></el-table-column>
      <el-table-column prop="contactTel" align="center" label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">{{scope.row.type | fitertype}}</template>
      </el-table-column>
      <el-table-column prop="serviceTel" align="center" label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" align="center" width="190">
        <template slot-scope="scope">
          <el-button
            :disabled="!scope.row.status"
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          ></el-button>
          <el-button
            @click="handleroom(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-menu"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[15, 30, 50, 100]"
        :page-size="15"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      :title="disval==1?'':''"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name" placeholder="" maxlength="100" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="ruleForm.address" placeholder="" maxlength="200" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomNo">
          <el-input
            v-model="ruleForm.roomNo"
            placeholder=""
            type="number"
            oninput="if(value.length>11)value=value.slice(0,11)"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="contactName">
          <el-input v-model="ruleForm.contactName" placeholder="" maxlength="10" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="contactTel">
          <el-input
            v-model="ruleForm.contactTel"
            placeholder=""
            type="number"
            oninput="if(value.length>11)value=value.slice(0,11)"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="serviceTel">
          <el-input
            v-model="ruleForm.serviceTel"
            placeholder=""
            type="number"
            oninput="if(value.length>20)value=value.slice(0,20)"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="areaId">
          <el-select v-model="ruleForm.areaId" clearable filterable placeholder="">
            <el-option v-for="item in arealist" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="ruleForm.type" placeholder="">
            <el-option v-for="item in typelist" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="ruleForm.status" placeholder="">
            <el-option
              v-for="item in statuslist"
              :key="item.id"
              :label="item.label"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="ruleForm.remark"
            type="textarea"
            :rows="2"
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
var cheackNumber = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error(""));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (value < 0) {
        callback(new Error("0"));
      } else {
        if (Number.isInteger(value)) {
          callback();
        } else {
          callback(new Error(""));
        }
      }
    } else {
      callback(new Error(""));
    }
  }, 100);
};

var checkPhone = (rule, value, callback) => {
  const tel = /^0\d{2,3}-?\d{7,8}$/;
  const phone = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
  if (!value) {
    return callback(new Error(""));
  }
  setTimeout(() => {
    if (!Number.isInteger(+value)) {
      callback(new Error(""));
    } else {
      if (tel.test(value)) {
        callback();
      } else if (phone.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    }
  }, 100);
};
export default {
  name: "hotel_list",
  data() {
    return {
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 15,
      loading: false,
      centerDialogVisible: false,
      term: {
        type: "",
        name: "",
        status: ""
      },
      arealist: [],
      hotellist: [],
      statuslist: [
        {
          id: 1,
          label: ""
        },
        {
          id: 2,
          label: ""
        }
      ],
      typelist: [
        {
          id: 1,
          label: ""
        },
        {
          id: 2,
          label: ""
        },
        {
          id: 3,
          label: ""
        },
        {
          id: 4,
          label: ""
        }
      ],
      ruleForm: {
        address: "",
        areaId: "",
        contactName: "",
        contactTel: "",
        id: "",
        name: "",
        remark: "",
        // replenishList: [],
        roomNo: "",
        serviceTel: "",
        status: "",
        type: ""
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        address: [
          { required: true, message: "", trigger: "blur" }
        ],
        roomNo: [{ required: true, validator: cheackNumber, trigger: "blur" }],
        contactName: [
          { required: true, message: "", trigger: "blur" }
        ],
        contactTel: [
          { required: true, validator: checkPhone, trigger: "blur" }
        ],
        serviceTel: [
          { required: true, validator: checkPhone, trigger: "blur" }
        ],
        areaId: [
          { required: true, message: "", trigger: "change" }
        ],
        type: [{ required: true, message: "", trigger: "change" }],
        status: [{ required: true, message: "", trigger: "change" }]
      }
    };
  },
  watch: {
    centerDialogVisible: function() {
      if (!this.centerDialogVisible) {
        for (let key in this.ruleForm) {
          if (this.ruleForm[key]) {
            this.ruleForm[key] = "";
          }
        }
        this.actuserobj = {};
      }
    }
  },
  filters: {
    fiterstatus: function(val) {
      if (val == 1) {
        return "";
      } else {
        return "";
      }
    },
    fitertype: function(val) {
      if (val == 1) {
        return "";
      } else if (val == 2) {
        return "";
      } else if (val == 3) {
        return "";
      } else if (val == 4) {
        return "";
      } else {
        return "";
      }
    }
  },
  methods: {
    //
    getarealist() {
      this.$http.get("hotel/area").then(res => {
        this.arealist = res.data;
      });
    },
    //
    //
    gethotellist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url = "hotel?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      for (let key in this.term) {
        if (this.term[key]) {
          _Url += "&" + key + "=" + this.term[key];
        }
      }
      this.$http.get(_Url).then(res => {
        let _data = res.data.data;
        for (let i in _data) {
          if (_data[i].status == 1) {
            _data[i].status = true;
          } else {
            _data[i].status = false;
          }
        }
        this.hotellist = _data;
        this.total = res.data.total;
      });
    },
    //
    handleCurrentChange(val) {
      this.pageNum = val;
      this.gethotellist();
    },
    //
    handleSizeChange(val) {
      this.pageSize = val;
      this.gethotellist();
    },
    //
    changestatus(obj) {
      let _Url = "hotel/status",
        _parms = {
          status: "",
          id: obj.id
        };
      if (obj.status) {
        _parms.status = 1;
      } else {
        _parms.status = 0;
      }
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("");
        }
      });
    },
    //
    openDialog(val) {
      this.disval = val;
      this.centerDialogVisible = !this.centerDialogVisible;
    },
    //
    handleedit(obj) {
      if (this.arealist.length < 1) {
        this.getarealist();
      }
      this.getidinfo(obj.id);
      this.openDialog(2);
      this.actuserobj = obj;
    },
    //
    handleroom(obj){
       this.$router.push({path: '/hotel/room/list', query: obj});
    },
    //ID
    getidinfo(id) {
      this.$http.get("hotel/" + id).then(res => {
        let obj = res.data;
        obj.type = Number(obj.type);
        obj.remark = obj.remark ? obj.remark : "";
        for (let key in obj) {
          // if (obj[key]) {
          this.ruleForm[key] = obj[key];
          // }
        }
      });
    },
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savedata();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    resetForm(formName) {
      if (this.actuserobj && this.actuserobj.id) {
        this.getidinfo(this.actuserobj.id);
      } else {
        this.$refs[formName].resetFields();
        this.ruleForm.remark = "";
      }
    },
    //
    savedata() {
      let _Url = "hotel",
        _parms = {};
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      if (this.disval == 1) {
        this.$http.post(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.gethotellist();
          }
        });
      } else if (this.disval == 2) {
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.gethotellist();
          }
        });
      }
    }
  },
  created() {
    this.gethotellist();
  }
};
</script>

<style lang="less">
.hotel_list {
  .topleft {
      .el-input {
        width: 25%;
      }
    }
}
</style>


